
body {
  padding: 0;
  margin: 0;
}











































#tab-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #f6f6f6;

  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  /* 加   $   的都是必须 */
  /* box-shadow:x偏移量（$） y偏移量($) 模糊度  阴影大小 颜色 是否内阴影; */
  -webkit-box-shadow: 0 -2px 1px rgba(100, 100, 100, 0.1);
          box-shadow: 0 -2px 1px rgba(100, 100, 100, 0.1);
}


.tab-bar-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  height: 49px;
}
.tab-bar-item img {
  width: 24px;
  height: 24px;
  /* img标签的对齐方式，这里让它不会和其它内容有3px的间隔 */
  vertical-align: middle;
}




















































